<template>
  <div class="panel panel-store-detail">
    <div class="panel-title">
      <div class="avator" @click.stop.prevent="showLeftMenuPopup=true">
        <img :src="userInfo.picture" alt="">
      </div>
      <van-search placeholder="一秒锁定您的课程" readonly disabled @click.native.stop="searchCourse" />
    </div>
    <div class="panel-body">
      <div class="banner-container">
        <van-swipe :show-indicators="false" :loop="true">
          <van-swipe-item v-for="(image, index) in images" :key="index">
            <img v-lazy="image.picture">
          </van-swipe-item>
        </van-swipe>
      </div>
      <div class="card" v-for="(category, index) in categoryList" :key="index">
        <div class="card-title">
          <h4>{{category.moduleName}}</h4>
        </div>
        <ul class="card-body">
          <li v-for="(course, i) in category.merchandise" :key="i" @click.stop.prevent="goTo(course.id)">
            <img :src="course.picture" alt="course logo">
            <div>
              <h5 class="flex-wrap"><span class="ellipsis">{{course.name}}</span><span class="hint mid-flex" v-if="false">限时优惠</span></h5>
              <div class="flex-wrap ellipsis"><b>可选</b><span class="mid-flex" v-for="period in course.payType.split(',')" :key="period">{{period}}</span></div>
            </div>
            <em class="mid-flex" data-unit="￥">{{course.amount / 100}}</em>
          </li>
        </ul>
      </div>
    </div>
    <van-popup v-model="showLeftMenuPopup" position="left">
      <left-menu></left-menu>
    </van-popup>
  </div>
</template>

<script>
import leftMenu from '@/components/left-menu.vue';
import { queryStoreDetail, queryImageData } from '@/api/index.js';

export default {
  name: 'store-detail',
  props: ['id'],
  components: {
    leftMenu,
  },
  computed: {
    userInfo() {
      return this.$store.state.userInfo;
    },
  },
  data() {
    return {
      images: ['/img/banner/banner-1.png'],
      showLeftMenuPopup: false,
      categoryList: [],
      page: 1,
      size: 10,
    };
  },
  created() {
    this.storeId = this.$route.params.id;
    this.queryData();
  },
  methods: {
    queryData() {
      queryStoreDetail(this.storeId, this.page, this.size)
        .then((result) => {
          if (Array.isArray(result.list)) {
            this.categoryList = result.list;
          }
        })
        .catch(console.error);
      queryImageData(this.storeId)
        .then((result) => {
          if (Array.isArray(result)) {
            this.images = result;
          }
        })
        .catch(console.error);
    },
    goTo(id) {
      this.$router.push(`/order-course/${id}`);
    },
  },
};
</script>

